"use client";

import { useState, useEffect } from "react";
import { Terminal, Search } from "lucide-react";
import { ResizableHandle, ResizablePanel, ResizablePanelGroup } from "@/components/ui/resizable";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Alert, AlertTitle, AlertDescription } from "@/components/ui/alert";
import { Skeleton } from "@/components/ui/skeleton";
import { Input } from "@/components/ui/input";

export default function HomePage() {
  const [estacoes, setEstacoes] = useState([]);
  const [estacaoSelecionada, setEstacaoSelecionada] = useState(null);
  const [pontos, setPontos] = useState([]);
  const [loadingEstacoes, setLoadingEstacoes] = useState(true);
  const [loadingPontos, setLoadingPontos] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [filtro, setFiltro] = useState("");

  useEffect(() => {
    async function fetchEstacoes() {
      try {
        const response = await fetch("/api/estacoes");
        const data = await response.json();
        setEstacoes(data);
      } catch {
        setError("Erro ao carregar as subestações");
      } finally {
        setLoadingEstacoes(false);
      }
    }
    fetchEstacoes();
  }, []);

  const estacoesFiltradas = estacoes.filter((est) =>
    est.nome.toLowerCase().includes(filtro.toLowerCase()) ||
    est.codigo.toLowerCase().includes(filtro.toLowerCase())
  );

  return (
    <ResizablePanelGroup direction="horizontal" className="h-screen border">
      {/* Sidebar */}
      <ResizablePanel defaultSize={25} className="bg-gray-50 border-r">
        {/* Campo de busca */}
        <div className="p-3">
          <div className="relative">
            <Search className="absolute left-2 top-2.5 h-4 w-4 text-gray-400" />
            <Input
              placeholder="Buscar subestação..."
              value={filtro}
              onChange={(e) => setFiltro(e.target.value)}
              className="pl-8"
            />
          </div>
        </div>

        {/* Lista de subestações */}
        <ScrollArea className="h-[calc(100%-60px)] custom-scroll">
          <ul className="space-y-1 px-2">
            {loadingEstacoes
              ? Array(8).fill(0).map((_, i) => (
                  <Skeleton key={i} className="h-6 w-full" />
                ))
              : estacoesFiltradas.map((est) => (
                  <li
                    key={est.codigo}
                    onClick={() => setEstacaoSelecionada(est)}
                    className={`cursor-pointer rounded-md px-3 py-2 text-sm transition-colors
                      ${estacaoSelecionada?.codigo === est.codigo
                        ? "bg-blue-100 text-blue-700 font-medium"
                        : "hover:bg-gray-100 text-gray-700"
                      }`}
                  >
                    {est.codigo}: {est.nome}
                  </li>
                ))}
          </ul>
        </ScrollArea>
      </ResizablePanel>

      <ResizableHandle />

      {/* Conteúdo */}
      <ResizablePanel>
        <div className="p-4">
          {estacaoSelecionada ? (
            <>
              <h1 className="text-xl font-bold mb-2">{estacaoSelecionada.nome}</h1>
              <p className="text-sm text-gray-500 mb-4">
                Código: {estacaoSelecionada.codigo} | Estação: {estacaoSelecionada.codigo}
              </p>

              {/* Tabela */}
              <div className="border rounded-lg overflow-hidden shadow-sm">
                <ScrollArea className="max-h-[75vh] custom-scroll">
                  <Table>
                    <TableHeader className="bg-gray-100 sticky top-0 z-10">
                      <TableRow>
                        <TableHead className="w-[250px]">ID do Ponto</TableHead>
                        <TableHead>Descrição</TableHead>
                        <TableHead className="text-right w-24">N° Ponto</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {pontos.map((ponto, index) => (
                        <TableRow
                          key={ponto.id}
                          className={index % 2 === 0 ? "bg-white" : "bg-gray-50"}
                        >
                          <TableCell className="font-mono text-xs">{ponto.id}</TableCell>
                          <TableCell className="text-sm">{ponto.descricao}</TableCell>
                          <TableCell className="text-right">{ponto.nPonto}</TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </ScrollArea>
              </div>
            </>
          ) : (
            <div className="flex h-full items-center justify-center">
              <Alert className="max-w-md">
                <Terminal className="h-4 w-4" />
                <AlertTitle>Nenhuma subestação selecionada</AlertTitle>
                <AlertDescription>
                  Selecione uma subestação na barra lateral para visualizar seus pontos.
                </AlertDescription>
              </Alert>
            </div>
          )}
        </div>
      </ResizablePanel>
    </ResizablePanelGroup>
  );
}
